                                       v-model="item.share"
                                       v-mask="'###'">
                            </div>
                        </div>
                    </div>

                </div>

                <div class="business-description">

                    <div class="business-description-header">
                        <div class="business-description-th title">3 основных поставщика (по обороту)</div>
                        <div class="business-description-th term">Срок сотрудничества</div>
                        <div class="business-description-th proportion">Доля в общем объеме реализации (в %)</div>
                    </div>

                    <div class="business-description-body">
                        <div v-for="(item, index) in mainSuppliers"
                             :key="index"
                             class="business-description-tr">
                            <div class="business-description-td title" data-title="Поставщик">
                                <input type="text"
                                       class="order-person-info-change-input"
                                       v-model="item.title">
                            </div>
                            <div class="business-description-td term" data-title="Срок сотрудничества">
                                <fg-checkbox v-model="item.term">Более 1 года</fg-checkbox>
                            </div>
                            <div class="business-description-td proportion" data-title="Доля в общем объеме реализации (в %)">
                                <input type="text"
                                       class="order-person-info-change-input"
                                       v-model="item.share"
                                       v-mask="'###'">
                            </div>
