        </div>

        <div class="order-person-info-footer-btns">
            <button class="btn btn-secondary"
                    @click.prevent="setOrderStepMinus">Назад</button>
            <button class="btn"
                    :class="{'loading': load}"
                    :disabled="load"
                    @click.prevent="confirmData">{{'Продолжить'}}</button>
        </div>

        <Popup v-if="errorModal"
               :error="true"
               :classes="'error-modal'"
               @close-modal="errorModal = false">

            <div class="popup-title">
                <div class="h3">Ошибка при заполнении</div>
            </div>

            <div class="popup-body">
                <p class="error-text">Введите обязательные поля</p>
                <button class="btn btn-lg btn-block close-modal">Хорошо</button>
            </div>

