</template>

<script>

    import {required, minLength} from 'vuelidate/lib/validators'
    import Popup from "@/components/Blocks/Popup";
    import {mapState} from 'vuex'

    export default {
        name: "CreateOrderStep6",
        components: {
            Popup
        },
        computed: {
            ...mapState({
                order: state => state.createOrder.orderFullInfo
            }),
            mainActivities() {
                return this.order.order.share.filter(item => item.type === 1)
            },
            mainSuppliers() {
                return this.order.order.share.filter(item => item.type === 2)
            },
            mainBuyers() {
                return this.order.order.share.filter(item => item.type === 3)
            },
        },
        data: () => ({
            errorModal: false,
            load: false,
        }),
        methods: {
            confirmData() {
                this.setOrderStepPlus()
            },
            setOrderStepPlus() {
                this.load = true
                this.$store.dispatch('updateOrderOnlyOne', {id: this.$route.params.id, fields: {share: this.order.order.share}}).then(() => {
                    this.$store.dispatch('nextStep')
                    this.load = false
                })
            },
            setOrderStepMinus() {
                this.$store.dispatch('prevStep')
            },
        }
    }
</script>
