<template>

    <div class="order-step-info">

        <div class="order-step-info-title">
            <div class="h2">Информация о заемщике</div>
            <p>Данные заполняются автоматически, если данные некорректны, свяжитесь со службой поддержки <a href="tel:8 800 77-518-77">8 800 77-518-77</a></p>
        </div>

        <div class="order-person-info-list">

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title">Полное наименование организации</div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{order.company.name}}</div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title">ИНН</div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{order.company.inn || 'Не указано'}}</div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title">КПП</div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{order.company.kpp || 'Не указано'}}</div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title">ОГРН</div>

                <div class="order-person-info-list-body">

                    <div class="h4" v-if="!ogrnChange">
                        {{order.order.ogrn}}
                    </div>

                    <div v-if="!ogrnChange"
                         class="order-person-info-edit">
                        <a @click.prevent="ogrnChange = !ogrnChange"
                           href="#">
                            <span class="icon-edit"></span>
                        </a>
                    </div>

                    <div v-if="ogrnChange"
                         class="order-person-info-change">
                        <input type="text"
                               class="order-person-info-change-input"
                               v-model="newOgrn"
                               @input="changedVal(true)">
                        <a @click.prevent="ogrnChange = !ogrnChange"
                           href="#"
                           class="small-link">Отмена</a>
                    </div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title">Дата создания организации</div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{order.company.c_create ? parseDate(order.company.c_create) : 'Не указано'}}</div>

                </div>
            </div>

        </div>

        <div class="order-person-info-footer-btns">
            <button class="btn btn-secondary"
                    @click.prevent="setOrderStepMinus">Назад</button>
            <button class="btn"
                    :disabled="load"
                    :class="{'loading': load}"
                    @click.prevent="setOrderStepPlus">{{changedData ? 'Сохранить и продолжить' : 'Продолжить'}}</button>
        </div>

        <Popup v-if="openInstructionModal" @close-modal="closeModal('openInstructionModal')">
            <div class="popup-title">
                <div class="h2"
                     v-html="$t('create_order.instruction_modal.title')" />
            </div>
            <div class="popup-body">
                <div v-html="$t('create_order.instruction_modal.descr')" />
                <button class="btn btn-lg btn-block close-modal">Все понятно</button>
            </div>
        </Popup>

    </div>

</template>

<script>

    import Popup from "@/components/Blocks/Popup"
    import {mapState} from 'vuex'
    import moment from "moment";

    export default {
        name: "CreateOrderStep2",
        components: {
            Popup
        },
        computed: {
            ...mapState({
                order: state => state.createOrder.orderFullInfo,
                currentOrder: state => state.createOrder
            })
        },
        data() {
            return {
                openInstructionModal: true,
                changedData: false,
                ogrnChange: false,
                load: false,
                newOgrn: ''
            }
        },
        methods: {
            setOrderStepPlus() {
                this.load = true
                if ( this.ogrnChange && this.changedData ) {
                    this.$store.dispatch('updateOrderOnlyOne', {
                        id: this.$route.params.id,
                        fields: {
                            ogrn: this.newOgrn
                        }
                    }).then(() => {
                        let id = this.$route.params.id
                        this.$store.dispatch('getCurrentOrder', id).then(resp => {
                            if ( this.currentOrder.orderFullInfo.company.ul ) {
                                this.$store.dispatch('nextStep').then(() => {
                                    this.load = false
                                })
                            }
                            else {
                                this.$store.dispatch('updateStep', 4).then(() => {
                                    this.load = false
                                })
                            }
                        })
                    })
                }
                else {
                    if ( this.currentOrder.orderFullInfo.company.ul ) {
                        this.$store.dispatch('nextStep').then(() => {
                            this.load = false
                        })
                    }
                    else {
                        this.$store.dispatch('updateStep', 4).then(() => {
                            this.load = false
                        })
                    }
                }
            },
            setOrderStepMinus() {
                this.$store.dispatch('prevStep')
            },
            changedVal(bool) {
                this.changedData = bool
            },
            closeModal(modal) {
                this[modal] = false
            },
            parseDate(date) {
                return moment(date).format('DD.MM.YYYY')
            }
        },
        mounted() {
            this.openInstructionModal = true
        },
    }
</script>

