<template>

    <div class="order-step-info">

        <div class="order-step-info-title">
            <div class="h2">Юридический адрес</div>
            <p>Данные заполняются автоматически, если данные некорректны, свяжитесь со службой поддержки <a href="tel:8 800 77-518-77">8 800 77-518-77</a></p>
        </div>

        <div v-if="currentOrder.orderFullInfo.company.ul || address.length !== 0"
             class="order-person-info-list">

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title">Индекс</div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{address.postcode || 'Не указано'}}</div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title">Регион</div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{address.region || 'Не указано'}}</div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title">Район</div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{address.district || 'Не указано'}}</div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title">Улица</div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{address.street || 'Не указано'}}</div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title">Дом</div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{address.home || 'Не указано'}}</div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title">Корпус</div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{address.corps || 'Не указано'}}</div>

                </div>
            </div>

            <div class="order-person-info-list-item">

                <div class="order-person-info-list-title">Кв / Офис</div>

                <div class="order-person-info-list-body">

                    <div class="h4">{{address.flat || 'Не указано'}}</div>

                </div>
            </div>

        </div>

        <div class="order-person-info-footer-btns">
            <button class="btn btn-secondary"
                    @click.prevent="setOrderStepMinus">Назад</button>
            <button class="btn"
                    :class="{'loading': load}"
                    :disabled="load"
                    @click.prevent="setOrderStepPlus">Продолжить</button>
        </div>

    </div>

</template>

<script>

    import {mapState} from 'vuex'

    export default {
        name: "CreateOrderStep3",
        data: () => ({
            load: false
        }),
        computed: {
            ...mapState({
                currentOrder: state => state.createOrder,
                order: state => state.createOrder.orderFullInfo,
            }),
            address() {
                return this.order.address.filter(address => address.type === 1)[0]
            }
        },
        methods: {
            setOrderStepPlus() {
                this.load = true
                this.$store.dispatch('nextStep').then(() => {
                    this.load = false
                })
            },
            setOrderStepMinus() {
                this.$store.dispatch('prevStep')
            },
            changedVal(bool) {
                this.$emit('canSaveAndNext', bool)
            }
        }
    }
</script>

