                <p>Выберите применяемую систему налогооблажения из списка:</p>
            </div>

            <div class="order-step-info-block-content">

                <div class="checkbox-group">

                    <div v-for="tax in taxationSystems"
                         :key="tax"
                         class="form-checkbox">
                        <label>
                            <input type="radio"
                                   name="tax-radio"
                                   :value="tax"
                                   v-model="order.order.taxationSystem">
                            <span>{{tax}}</span>
                        </label>
                    </div>

                </div>

            </div>

        </div>

        <div class="order-step-info-block">

            <div class="order-step-info-block-title">
                <div class="h3">{{order.company.ul ? 'Фактический адрес' : 'Юридический адрес'}}</div>
                <p>Заполните следующие поля:</p>
            </div>

            <div class="order-step-info-block-content">

                <div class="order-person-info-list">

                    <div class="order-person-info-list-item">

                        <div class="order-person-info-list-title">Индекс <span class="red">*</span></div>

                        <div class="order-person-info-list-body">

                            <div class="order-person-info-change">
                                <input v-if="order.company.ul"
                                       type="text"
                                       v-mask="'######'"
                                       class="order-person-info-change-input"
                                       v-model="address2.postcode"
                                       :class="{invalid: $v.address2.postcode.$error ? (!$v.address2.postcode.required || !$v.address2.postcode.minLength) : false }">
                                <input v-else
                                       type="text"
                                       v-mask="'######'"
                                       class="order-person-info-change-input"
                                       v-model="address1.postcode"
                                       :class="{invalid: $v.address1.postcode.$error ? (!$v.address1.postcode.required || !$v.address1.postcode.minLength) : false }">
                            </div>

                        </div>

                    </div>

                    <div class="order-person-info-list-item">

                        <div class="order-person-info-list-title">Город <span class="red">*</span></div>

                        <div class="order-person-info-list-body">

                            <div class="order-person-info-change">
                                <input v-if="order.company.ul"
                                       type="text"
                                       class="order-person-info-change-input"
                                       v-model="address2.city"
                                       :class="{invalid: $v.address2.city.$error ? !$v.address2.city.required : false }">
                                <input v-else
                                       type="text"
                                       class="order-person-info-change-input"
                                       v-model="address1.city"
                                       :class="{invalid: $v.address1.city.$error ? !$v.address1.city.required : false }">
                            </div>

                        </div>

                    </div>

                    <div class="order-person-info-list-item">

                        <div class="order-person-info-list-title">Регион <span class="red">*</span></div>

                        <div class="order-person-info-list-body">

                            <div class="order-person-info-change">
                                <input v-if="order.company.ul"
                                       type="text"
                                       class="order-person-info-change-input"
                                       v-model="address2.region"
                                       :class="{invalid: $v.address2.region.$error ? !$v.address2.region.required : false }">
                                <input v-else
                                       type="text"
                                       class="order-person-info-change-input"
                                       v-model="address1.region"
                                       :class="{invalid: $v.address1.region.$error ? !$v.address1.region.required : false }">
                            </div>

                        </div>

                    </div>

                    <div class="order-person-info-list-item">

                        <div class="order-person-info-list-title">Район</div>

                        <div class="order-person-info-list-body">

                            <div class="order-person-info-change">
                                <input v-if="order.company.ul"
                                       type="text"
                                       class="order-person-info-change-input"
                                       v-model="address2.district">
                                <input v-else
                                       type="text"
                                       class="order-person-info-change-input"
                                       v-model="address1.district">
                            </div>

                        </div>

                    </div>

                    <div class="order-person-info-list-item">

                        <div class="order-person-info-list-title">Улица <span class="red">*</span></div>

                        <div class="order-person-info-list-body">

                            <div class="order-person-info-change">
                                <input v-if="order.company.ul"
                                       type="text"
                                       class="order-person-info-change-input"
                                       v-model="address2.street"
                                       :class="{invalid: $v.address2.street.$error ? !$v.address2.street.required : false }">
                                <input v-else
                                       type="text"
                                       class="order-person-info-change-input"
                                       v-model="address1.street"
                                       :class="{invalid: $v.address1.street.$error ? !$v.address1.street.required : false }">
                            </div>

                        </div>

                    </div>

                    <div class="order-person-info-list-item">

                        <div class="order-person-info-list-title">Дом <span class="red">*</span></div>

                        <div class="order-person-info-list-body">

                            <div class="order-person-info-change">
                                <input v-if="order.company.ul"
                                       type="text"
                                       class="order-person-info-change-input"
                                       v-model="address2.home"
                                       :class="{invalid: $v.address2.home.$error ? !$v.address2.home.required : false }">
                                <input v-else
                                       type="text"
                                       class="order-person-info-change-input"
                                       v-model="address1.home"
                                       :class="{invalid: $v.address1.home.$error ? !$v.address1.home.required : false }">
                            </div>

                        </div>

                    </div>

                    <div class="order-person-info-list-item">

                        <div class="order-person-info-list-title">Корпус</div>

                        <div class="order-person-info-list-body">

                            <div class="order-person-info-change">
                                <input v-if="order.company.ul"
                                       type="text"
                                       class="order-person-info-change-input"
                                       v-model="address2.corps">
                                <input v-else
                                       type="text"
                                       class="order-person-info-change-input"
                                       v-model="address1.corps">
                            </div>

                        </div>

                    </div>

                    <div class="order-person-info-list-item">

                        <div class="order-person-info-list-title">Кв / Офис</div>

                        <div class="order-person-info-list-body">

                            <div class="order-person-info-change">
                                <input v-if="order.company.ul"
                                       type="text"
                                       class="order-person-info-change-input"
                                       v-model="address2.flat">
                                <input v-else
                                       type="text"
                                       class="order-person-info-change-input"
                                       v-model="address1.flat">
                            </div>

                        </div>

                    </div>

                </div>

            </div>

        </div>

        <div class="order-person-info-footer-btns">
            <button class="btn btn-secondary"
                    @click.prevent="setOrderStepMinus">Назад</button>
            <button class="btn"
                    :class="{'loading': load}"
                    :disabled="load"
                    @click.prevent="confirmData">{{'Продолжить'}}</button>
        </div>

        <Popup v-if="errorModal"
               :error="true"
               :classes="'error-modal'"
               @close-modal="errorModal = false">

            <div class="popup-title">
                <div class="h3">Ошибка при заполнении</div>
            </div>

            <div class="popup-body">
                <p class="error-text">Введите обязательные поля</p>
                <button class="btn btn-lg btn-block close-modal">Хорошо</button>
            </div>

        </Popup>

    </div>

</template>

<script>

    import {required, minLength} from 'vuelidate/lib/validators'
    import Popup from "@/components/Blocks/Popup";
    import {mapState} from 'vuex'

    export default {
        name: "CreateOrderStep6",
        components: {
            Popup
        },
        validations: {
            address2: {
                postcode: {required, minLength: minLength(6)},
                city: {required},
                region: {required},
                street: {required},
                home: {required},
            },
            address1: {
                postcode: {required, minLength: minLength(6)},
                city: {required},
                region: {required},
                street: {required},
                home: {required},
            }
        },
        computed: {
            ...mapState({
                fields: state => state.createOrder.fields,
                order: state => state.createOrder.orderFullInfo,
                taxationSystems: state => state.createOrder.taxationSystems
            }),
            address1() {
                return this.order.address.filter(item => item.type === 1)[0]
            },
            address2() {
                return this.order.address.filter(item => item.type === 2)[0]
            }
        },
        data: () => ({
            errorModal: false,
            load: false,
        }),
        methods: {
            confirmData() {
                if ( this.order.company.ul ) {
                    if (this.$v.address2.$invalid) {
                        this.$v.address2.$touch();
                        this.errorModal = true
                        return
                    }
                }
                else {
                    if (this.$v.address1.$invalid) {
                        this.$v.address1.$touch();
                        this.errorModal = true
                        return
                    }
                }
                this.setOrderStepPlus()
            },
            setOrderStepPlus() {
                this.load = true
                if ( this.order.company.ul ) {
                    for ( let key in this.address2 ) {
                        if ( this.address2[key] ) {
                            if ( key === 'postcode' ) this.address2[key] = parseInt(this.address2[key])
                            if ( key !== 'type' ) this.address2[key] = this.address2[key].toString()
                        }
                    }
                }
                else {
                    for ( let key in this.address1 ) {
                        if ( key !== 'type' && this.address1[key] ) this.address1[key] = this.address1[key].toString()
                    }
                }
                let updatedData = {
                    address: [
                        this.order.company.ul ? this.address2 : this.address1
                    ],
                    taxationSystem: this.order.order.taxationSystem
                }
                this.$store.dispatch('updateOrderOnlyOne', {id: this.$route.params.id, fields: updatedData}).then(() => {
                    this.$store.dispatch('nextStep').then(() => this.load = false)

                })
            },
            setOrderStepMinus() {
                if ( this.order.company.ul ) {
                    this.$store.dispatch('prevStep')
                }
                else {
                    this.$store.dispatch('updateStep', 4)
                }
            },
        }
    }
</script>
